import React, { Component, PropTypes } from 'react'
import { bindActionCreators } from 'redux'
import { connect } from 'react-redux'
import * as action  from '../actions'
import Counter from '../components/Counter'
import TitleBar from '../components/TitleBar'
import { Link } from 'react-router'

class ProductInfo extends Component {
  static contextTypes = {
      router: PropTypes.object.isRequired
  }
  constructor(props){
      super(props);
  }
  componentDidMount() {
    const { id } = this.props.params;
    this.props.actions.getProductById(id)
  }
  handleChange(num){
    this.props.productInfo.number = num;
  }
  handleBack() {
    this.context.router.goBack()
  }
  turnToCart() {
    this.context.router.push("/cart");
  }
  render() {
    const { productInfo, actions } = this.props;
    const { id, name, pic, disc, price, num, number} = productInfo

    return (
      <div className="product-info">
        <TitleBar title="商品详情" handleBack={this.handleBack.bind(this)} />
        <div className="product-container">
          <div className="productInfo-img">
            <img src={pic} />
            <h2>{name}</h2>
            <p>￥{price}元</p>
          </div>
          <div className="product-num">
            <div className="num-box">
            <span>数量</span>
            <Counter
              maxNum={num}
              number={number}
              handleChange={num => this.handleChange(num)}
              />
            <span>库存{num}件</span>
            </div>
          </div>
          <div className="product-content">
            <p>商品信息</p>
            <p>{disc}</p>
          </div>
        </div>
        <div className="productinfo-go">
          <div className="productInfo-cart" onClick={this.turnToCart.bind(this)}><span></span></div>
          <div className="productinfo-btn-cart" onClick={number => actions.addToCart(id, number)} >加入购物车</div>
        </div>
      </div>
    )
  }
}

ProductInfo.propTypes = {
  productInfo: PropTypes.object.isRequired,
  actions: PropTypes.object.isRequired
}

export default connect(
  state => ({
    productInfo: state.productInfo
  }),
  dispatch => ({
    actions: bindActionCreators(action, dispatch)
  })
)(ProductInfo)



